<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入广告编号或用户ID"
                      @keyup.native.enter="getlist"
                      v-model="matchQuery.keyword">
            </el-input>
            <span style="margin-left: 20px;margin-right: 10px">状态：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.tradeState">
                <el-option v-for="typeStatus in statusOptions" :key="typeStatus.key"
                           :label="typeStatus.status_name"
                           :value="typeStatus.key">
                </el-option>
            </el-select>
            <span style="margin-left: 20px;margin-right: 10px">虚拟币类型：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.coinID">
                <el-option v-for="typeStatus in cionlist" :key="typeStatus.key"
                           :label="typeStatus.englishName"
                           :value="typeStatus.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="typeQuery" size="small">查询</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100">
                <template scope="scope" class="filter-operation">
                    <div v-if="scope.row.tradeState===1">
                        <el-button
                                type="text" size="small"
                                @click="downadverti(scope.row.id)" v-if="true">下架广告
                        </el-button>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="matchQuery.page"
                           :page-sizes="[10,20,30,50]" :page-size="matchQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<script>

    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {getAdvertisingTransactionList, deleteAdvertising} from '@/api/otcdealorder';
    import {virtualCoinAndGoods} from '@/api/virtual';

    const statusOptions = [
        {key: '', status_name: '全部状态'},
        {key: '1', status_name: '进行中'},
        {key: '2', status_name: '关闭中'},
        {key: '3', status_name: '已删除'},
    ];

    export default {
        data() {
            return {
                total: null,
                itemWidth: '160px',
                dialogVisible: false,
                feesDialogVisible: false,
                tableData: [],
                cionlist: null,
                matchQuery: {
                    keyword: '',
                    tradeState: '',
                    coinID: '',
                    page: 1,
                    pageSize: 10
                },
                statusOptions,
                action: '',
                tableHeader: [
                    {
                        label: "广告编号",
                        prop: "id",
                        width: 80
                    }, {
                        label: "交易",
                        prop: "tradeType",
                        width: 100,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '买入' : (value === 2 ? '卖出' : '');
                        }

                    }, {
                        label: "会员",
                        prop: "realname",
                        width: 120
                    }, {
                        label: "资产名称",
                        prop: "coinName",
                        width: 80
                    }, {
                        label: "状态",
                        prop: "tradeState",
                        width: 100,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '进行中' : (value === 2 ? '已关闭' : (value === 3 ? '已删除' : ''));
                        }
                    }, {
                        label: "取价类型",
                        prop: "priceType",
                        width: 80,
                        formatter(row, column, value) {
                            return value === 1 ? '固定价格' : (value === 2 ? '市场价格' : '');
                        }
                    }, {
                        label: "溢价",
                        prop: "premium",
                        width: 120
                    }, {
                        label: "价格",
                        prop: "price",
                        width: 120
                    }, {
                        label: "交易限额",
                        prop: "minPrice",
                        width: 120
                    }, {
                        label: "交易限额",
                        prop: "maxPrice",
                        width: 120,
                    }, {
                        label: "付款方式",
                        prop: "payType",
                        width: 120,
						formatter(row, column, value) {
                            return value !== null ? (value === 0 ? '' : value.toString().replace(1,'银行卡').replace(2,'支付宝').replace(3,'微信')) : '';
                        }
                    }, {
                        label: "付款期限",
                        prop: "payTime",
                        width: 120
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }],
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },

        methods: {
            typeQuery() {
                this.getlist();
            },
            getlist() {
                getAdvertisingTransactionList(this.matchQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.total = response.data.totalItems;
                        this.listLoading = false;
                    }
                })
            },
            //翻页的按钮
            userPage(val) {
                this.matchQuery.page = val;
                this.getlist();
            },
            //获取每页多少条
            userPageSize(val) {
                this.matchQuery.pageSize = val;
                this.getlist();
            },
            getcoinlist() {
                let _this = this;
                virtualCoinAndGoods([1,3], 2).then(response => {
                    if (response.result) {
                        _this.cionlist = response.data;
                    }
                })
            },
            downadverti(id) {
                this.$confirm('确认下架？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteAdvertising(id).then(response => {
                        if (response.result) {
                            this.getlist();
                        }
                        this.getMessage(response.msg,response.result);
                    })
                }).catch(() => {

                });
            }
        },
        created() {
            this.getlist();
            this.getcoinlist();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }
</style>


