import fetch from '@/utils/fetch'

//搜索所有
export function virtualcoin(query) {
    return fetch({
        url: 'admin/virtualcoinbases/query',
        method: 'post',
        data: query
    })
}


//添加币种
export function increasedVirtualcoin(query) {
    return fetch({
        url: 'admin/virtualcoin',
        method: 'post',
        data: query
    })
}


//启用禁用一个虚拟币
export function enableDisableCoin(query) {
    return fetch({
        url: 'admin/virtualcoinbase' + query,
        method: 'put',
    })
}

//查询虚拟币详细信息
export function virtualCoinName(query) {
    return fetch({
        url: 'admin/virtualcoinbase/' + query,
        method: 'get',
    })
}

export function amendCoinBase(query, id) {
    return fetch({
        url: '/admin/virtualcoin/' + id,
        method: 'put',
        data: query
    })
}

// 测试钱包
export function testCoinWallet(id) {
    return fetch({
        url: 'admin/virtualcoin/' + id + '/testwallet',
        method: 'get',
    })
}

export function postgoodspage(query) {
    return fetch({
        url: '/admin/postgoodsPage',
        method: 'post',
        data: query
    })
}

export function postgoods(query) {
    return fetch({
        url: '/admin/postgoods',
        method: 'post',
        data: query
    })
}

export function getgoods(query) {
    return fetch({
        url: '/admin/getgoods/' + query,
        method: 'get',
    })
}

export function putgoods(query) {
    return fetch({
        url: '/admin/putgoods',
        method: 'put',
        data: query
    })
}

export function putgoodsstatus(query) {
    return fetch({
        url: '/admin/putgoodsstatus' + query,
        method: 'put',
        data: query
    })
}

export function virtualCoinAndGoods(types, status ,entityFlag) {
    return fetch({
        url: '/admin/virtualcoinandgoods',
        method: 'put',
        data: {types, status,entityFlag}
    }).catch();
}

export function getNewAddress(params) {
    return fetch({
        url: 'admin/virtualcoin/' + params.id + '/produceaddress',
        method: 'get',
        params: {size: params.size, password: params.password}
    })
}




