import fetch from '@/utils/fetch'

export function getAdvertisingTransactionList(query) {
    return fetch({
        url: 'admin/advertising/advertorder',
        method: 'get',
        params: query
    })
}

export function getTradeOrderList(query) {
    return fetch({
        url: 'admin/transaction/tradeorder',
        method: 'get',
        params: query
    })
}

export function updateTradeCancel(query) {
    return fetch({
        url: 'admin/transaction/cancel',
        method: 'put',
        data: query
    })
}
export function updateTradePass(query) {
    return fetch({
        url: 'admin/transaction/pass',
        method: 'put',
        data: query
    })
}
export function deleteAdvertising(query) {
    return fetch({
        url: 'admin/advertising/'+query+'/delete',
        method: 'put',
        data: query
    })
}




